#{$root-selector} {

  /*! Variables */

  --pt-input-box-shadow: inset 0 0 0 1px var(--button-outlined-border-color);
  --pt-dark-input-box-shadow: inset 0 0 0 1px var(--button-outlined-border-color);

  // --input-box-shadow-focus: inset 0 0 0 2px hslax($input-shadow-color-focus, 1); // custom

}

#{$root-selector} {

  // var(--button-outlined-border-color)
  .#{$ns}-input {
    box-shadow: inset 0 0 0 1px var(--button-outlined-border-color);

    &:focus,
    &.#{$ns}-active {
      box-shadow: inset 0 0 0 2px var(--input-shadow-color-focus);
    }
  }


  .#{$ns}-control.#{$ns}-switch input {
    &~.#{$ns}-control-indicator {
      background-color: var(--button-background-color);
      box-shadow: var(--pt-input-box-shadow) !important;

      &:before {
        background-color: var(--pt-text-color-muted);
        box-shadow: none;
      }
    }

    &:checked~.#{$ns}-control-indicator {
      background: var(--switch-checked-background-color);
      box-shadow: none !important;

      &:before {
        background-color: var(--white);
        box-shadow: none;
      }
    }
  }

}
